.imgBox{
    width: 100vw;
    height: 100vh;
    display: flex;
    position: relative;
    right: 96px;
    bottom: 100px;
    justify-content: center;
    align-items: center;
}
.imgBox>div{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border-bottom: 2px solid rgb(52, 52, 52);
    animation: rotation 0.3s infinite linear;
}
@keyframes rotation {
    to{
        transform: rotate(360deg);
    }
}
.header{
    margin: 5px;
    box-shadow: 0px 10px 20px 1px rgb(110, 110, 110);
    margin-bottom:80px;
}